export default {
	data() {
		return {
			cuIconList: [{
				badge: 0,
				name: '1#101'
			}, {
				color: 'orange',
				badge: 1,
				name: '1#102'
			}, {
				color: 'yellow',
				badge: 0,
				name: '1#103'
			}, {
				badge: 0,
				name: '1#104'
			}, {
				badge: 0,
				name: '1#105'
			}, {
				badge: 0,
				name: '1#106'
			}, {
				badge: 0,
				name: '1#107'
			}, {
				badge: 0,
				name: '1#108'
			}, {
				badge: 0,
				name: '1#109'
			}, {
				badge: 0,
				name: '1#110'
			}],
			modalName: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null,
		};
	},
	methods: {
		openMessage(e) {
			uni.navigateTo({
				url: '/pages/order/add'
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},

		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}
}
